<template>
<div>
  <!-- <button @click="login()">登陆</button>
  <button @click="logout()">退出</button>
  <button @click="getImg()">获取</button> -->
  <img id="img" />
</div>
</template>

<script>
var requestFailImg =
  'data:image/png;base64,'
var warningImg =
  'data:image/png;base64,'

import {requestCounterAdd,requestCounterMinus} from '../utils/tools.js'
let instance = axios.create()
instance.interceptors.request.use(requestCounterAdd)
const component = {
  name: 'work-flow',
  data() {
    return {}
  },
  props: {
    requestUrl: String,
    instId: String,
    defId: String,
    // account: String,
  },
  install: (Vue, options) =>
    Vue.component(
      (options ? options.prefix : '') + component.name,
      component,
    ),
  methods: {
    imageEncode(arrayBuffer, mimetype) {
      console.debug(typeof arrayBuffer)
      let u8 = new Uint8Array(arrayBuffer)
      let b64encoded = btoa(
        [].reduce.call(
          new Uint8Array(arrayBuffer),
          function(p, c) {
            return p + String.fromCharCode(c)
          },
          ''
        )
      )
      return 'data:' + mimetype + ';base64,' + b64encoded
    },
    // login() {
    //   return new Promise((resolve, reject) => {
    //     var params = {
    //       password: this.account,
    //       account: this.account
    //     }
    //     instance.get('org/login/valid', {
    //       params,
    //       withCredentials: true
    //     }).then(resp => {
    //       console.debug(resp)
    //       resolve()
    //     })
    //   })
    // },
    // logout() {
    //   instance.get('logout', {
    //       withCredentials: true
    //     })
    //     .catch(e => {
    //       console.log(e);
    //     })
    // },
    getImg() {
      return new Promise((resolve, reject) => {
        var params = {
          instId: this.instId,
          defId: this.defId
        }
        instance({
            method: 'get',
            url: 'InfoFile/Activity',
            params: params,
            // withCredentials: true,
            responseType: 'arraybuffer'
          })
          // instance.post('/file/activity', params)
          .then(resp => {
            requestCounterMinus()
            img.src = resp.headers['content-type'].indexOf('image') > -1 ? this.imageEncode(resp.data, resp.headers['content-type']) : warningImg
            resolve()
          })
          .catch(() => {
            requestCounterMinus()
            img.src = requestFailImg
            resolve()
          })
      })
    },
    // refresh() {
    //   this.login().then(() => {
    //     this.getImg().then(() => this.logout())
    //   })
    // },

    // async test() {
    //   await this.login()
    //   await this.getImg()
    //   await this.logout()
    // }
  },
  created() {
    instance.defaults.baseURL = this.requestUrl
  },
  // mounted() {
  //   this.getImg()
    // this.refresh()
    // this.test()
  // }
}
export default component
</script>

